'use client';

import Link from 'next/link';
import { useState, useMemo } from 'react';
import fs from 'fs';
import path from 'path';

export const dynamic = 'force-static';

const pages = JSON.parse(
  fs.readFileSync(path.join(process.cwd(), 'data', 'pages.json'), 'utf8')
);

export default function HomePage() {
  const [search, setSearch] = useState('');
  const [tech, setTech] = useState('');
  const [location, setLocation] = useState('');
  const [category, setCategory] = useState('');
  const [dark, setDark] = useState(false);

  const unique = (field) => [...new Set(pages.map(p => p[field]))];

  const filteredPages = useMemo(() => {
    return pages.filter((p) =>
      p.title.toLowerCase().includes(search.toLowerCase()) &&
      (!tech || p.tech === tech) &&
      (!location || p.location === location) &&
      (!category || p.category === category)
    );
  }, [search, tech, location, category]);

  return (
    <main className={`${dark ? 'bg-gray-900 text-white' : 'bg-gray-50 text-gray-800'} min-h-screen py-12 px-6 sm:px-12`}>
      <div className="max-w-6xl mx-auto">
        <div className="flex justify-between items-center mb-6">
          <h1 className="text-3xl sm:text-4xl font-bold">üîç SEO Pages Explorer</h1>
          <button
            className="text-sm px-4 py-1 border rounded-md"
            onClick={() => setDark(!dark)}
          >
            {dark ? '‚òÄÔ∏è Light Mode' : 'üåô Dark Mode'}
          </button>
        </div>

        <div className="grid grid-cols-1 sm:grid-cols-4 gap-4 mb-6">
          <input
            className="col-span-2 p-2 rounded border w-full"
            placeholder="Search by title..."
            value={search}
            onChange={(e) => setSearch(e.target.value)}
          />

          <select className="p-2 rounded border" value={tech} onChange={(e) => setTech(e.target.value)}>
            <option value="">All Tech</option>
            {unique('tech').map((t) => (
              <option key={t}>{t}</option>
            ))}
          </select>

          <select className="p-2 rounded border" value={location} onChange={(e) => setLocation(e.target.value)}>
            <option value="">All Locations</option>
            {unique('location').map((l) => (
              <option key={l}>{l}</option>
            ))}
          </select>
        </div>

        <div className="mb-4">
          <select className="p-2 rounded border" value={category} onChange={(e) => setCategory(e.target.value)}>
            <option value="">All Categories</option>
            {unique('category').map((c) => (
              <option key={c}>{c}</option>
            ))}
          </select>
        </div>

        <div className="grid gap-6 sm:grid-cols-2 lg:grid-cols-3">
          {filteredPages.map((page) => (
            <Link href={`/${page.slug}`} key={page.slug}>
              <div className={`rounded-xl border p-6 hover:shadow-lg transition cursor-pointer ${dark ? 'bg-gray-800 border-gray-700' : 'bg-white border-gray-100'}`}>
                <h2 className="text-lg font-semibold mb-2">{page.title}</h2>
                <p className="text-sm mb-2">{page.description}</p>
                <div className="flex flex-wrap gap-2 text-xs text-gray-500 dark:text-gray-400">
                  <span>üìç {page.location}</span>
                  <span>üíª {page.tech}</span>
                  <span>üìÅ {page.category}</span>
                </div>
              </div>
            </Link>
          ))}
        </div>

        <p className="text-center text-xs mt-12 text-gray-400 dark:text-gray-500">
          ¬© {new Date().getFullYear()} Kodingcircle. All rights reserved.
        </p>
      </div>
    </main>
  );
}
